/* modules */
.hamburger {
  &--header {
    opacity: 1 !important;
    padding: 0 15px;
    height: 30px;
    width: 180px;
    position: relative;
    text-align: center;
    // display: none
    margin: 0 auto;
    z-index: 100;
    white-space: nowrap;
    .hamburger-box {
      width: 14px;
      height: 12px;
      top: 1px;
 }      // background-color: pink
    .hamburger-inner {
      &::before, &::after {
        background-color: $color-white;
        border-radius: 0;
        width: 14px;
        height: 2px; }
      &::before {
        top: 5px !important; }
      &::after {
        top: 10px !important; }
      background-color: $color-white;
      border-radius: 0;
      width: 14px;
      height: 2px;
      margin-top: 0;
      top: 0 !important; }
    &.is-active {
      .hamburger-inner {
        transform: translate3d(0, 5px, 0) rotate(45deg);
        background-color: $color-white;
        &::before, &::after {
          background-color: $color-white; }
        &::before {
          transform: rotate(-45deg) translate3d(-5.71429px, -2px, 0); }
        &::after {
          transform: translate3d(0, -10px, 0) rotate(-90deg); } }
      strong {
        color: $color-white; } }
    strong {
      color: $color-white;
      text-transform: uppercase;
      font-size: rem(14);
      font-family: $font-intro;
      margin-left: 7px;
 } } }      // position: absolute

.tab-pane {
  display: none;
  &.active {
    display: block; } }

// table
div.table {
  display: table;
  &>div {
    display: table-cell;
    vertical-align: top;
    &+div {
      padding-left: 30px; } } }

// Table
table.table {
  margin-bottom: 15px;
  td {
    padding: 5px 0;
    p {
      &:last-child {
        margin-bottom: 0; } }
    &+td {
      padding-left: 15px; } } }

.visible-xxs-inline-block {
  display: none !important; }

/* driver */
.driver {
  padding-top: 112px;
  padding-bottom: 0;
  overflow: hidden;
  &__block {
    background: $color-red;
    height: 290px;
    width: 435px;
    color: $color-white;
    padding-top: 67px;
    transform: skewX(8deg);
    position: relative;
    z-index: 10;
    float: left;
    &__wrapper {
      transform: skewX(-8deg); } }
  &__title {
    font-family: $font-intro;
    text-transform: uppercase;
    font-size: rem(34);
    text-align: right;
    padding-right: 62px;
    line-height: 1;
    margin-bottom: 43px;
    small {
      font-size: rem(14);
      display: block;
      line-height: 1;
      margin-top: 23px; } }

  &__img {
    position: relative;
    z-index: 5;
    float: left;
    margin-top: -10px;
    margin-left: -16px;
    margin-bottom: -30px; }

  &__figure {
    float: right;
    margin-top: 175px;
    right: 84px;
    position: relative;
    z-index: 20;
    &>div {
      &:first-child {
        border: 2px solid $color-red;
        height: 114px;
        width: 171px;
        transform: skewX(7deg); }
      &:last-child {
        height: 58px;
        background-color: $color-yellow;
        width: 86px;
        transform: skewX(7deg);
        margin-top: -58px;
        margin-left: 127px;
        position: relative;
        z-index: 5;
        .icon {
          position: absolute;
          left: 23px;
          top: 15px;
          transform: skewX(-7deg); }
        // &::after
        //   content: ''
        //   @include sprite($icon-taxi)
        //   position: absolute
        //   left: 50%
        //   top: 50%
 } } } }        //   transform: translate(-50%, -50%) skewX(-7deg)

/* additional */
.additional {
  background: $color-lightgray;
  padding: 0;
  overflow: hidden;
  .container {
    position: relative; }
  &__top, &__bottom {
    position: absolute;
    width: 100%;
    top: 0;
    background-color: $color-white;
    transform: skewX(-20deg); }
  &__top {
    top: 0;
    height: 54px;
    margin-right: 430px;
    right: 0; }
  &__bottom {
    top: inherit;
    bottom: 0;
    height: 25px;
    margin-left: 780px; }
  &__wrapper {
    min-height: 244px;
    padding-top: 95px;
    padding-bottom: 30px; }
  &__title {
    text-transform: uppercase;
    color: $color-red;
    font-size: rem(25);
    font-family: $font-intro;
    line-height: 1;
    text-align: right;
    margin-top: -63px;
    margin-bottom: 40px; }
  &__list {
    margin: 0;
    padding: 0;
    font-size: rem(14);
    @include clearfix;
    li {
      list-style: none;
      width: 33.3%;
      float: left;
      margin: 0;
      padding: 0 15px;
      &>div {
        position: relative;
        height: 43px;
        padding-left: 68px;
        padding-right: 80px;
        padding-top: 10px;
        &>i.icon {
          position: absolute;
          left: 0;
          top: 0; }
        &>strong {
          position: absolute;
          right: 0;
          color: $color-red;
          white-space: nowrap;
          top: 14px;
          line-height: 1;
          i {
            position: relative;
            left: -1px;
            top: 1px; } }
        &>span {
          display: inline-block;
          line-height: 1;
          small {
            font-size: rem(11); } } } } } }

/* about */
.about {
  position: relative;
  padding: 0;
  // height: 705px
  overflow: hidden;
  background: {
    color: $color-black;
    position: top center;
    repeat: no-repeat;
    image: url('../img/about-background.png'); }

  &__wrapper {
    height: 705px;
    position: relative;
    width: 100%; }

  &__button {
    position: absolute;
    right: 0;
    top: 84px;

    &>a {
      display: block;
      height: 120px;
      background: $color-white;
      width: 325px;
      position: absolute;
      font-size: rem(30);
      text-transform: uppercase;
      font-family: $font-intro;
      color: $color-dark;
      right: 0;
      &::before {
        position: absolute;
        left: -23px;
        height: 0;
        width: 0;
        border-top: 120px solid transparent;
        border-right: 23px solid $color-white;
        content: '';
        transition: border-color 0.35s; }
      &:hover, &:focus {
        text-decoration: none;
        color: $color-white;
        background-color: $color-red;
        &::before {
          border-right-color: $color-red; } }
      span {
        position: absolute;
        bottom: 13px;
        right: 17px;
        display: block;
        line-height: 1;
        white-space: nowrap; }
      &:last-child {
        margin-top: 134px;
        width: 352px; } } }

  &__partners {
    position: absolute;
    bottom: 130px;
    right: 0;
    text-align: right;
    &__title {
      height: 27px;
      line-height: 28px;
      background: $color-white;
      width: 233px;
      text-align: center;
      color: #dfa499;
      text-transform: uppercase;
      font-family: $font-intro;
      font-size: rem(14);
      margin-bottom: 9px;
      position: relative;
      display: inline-block;
      &::before {
        position: absolute;
        left: -5px;
        height: 0;
        width: 0;
        border-top: 27px solid transparent;
        border-right: 5px solid $color-white;
        content: ''; } }
    img {
      display: block; } }

  &__text {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 103px;
    font-family: $font-intro;
    text-transform: uppercase;
    &__line {
      position: absolute;
      width: 100%;
      top: 0;
      background-color: $color-white;
      transform: skewX(-20deg);
      height: 103px;
      margin-left: 445px; }
    &>span {
      display: block;
      line-height: 1;
      color: $color-white;
      font-size: rem(120);
      position: absolute;
      left: 5px;
      bottom: -18px; }
    &>strong {
      position: absolute;
      right: 5px;
      text-align: left;
      font-size: rem(60);
      color: $color-red;
      font-family: $font-intro;
      bottom: -3px;
      span, small {
        display: block;
        line-height: 1; }
      small {
        font-size: rem(21);
        color: #535353;
        margin-top: 14px; } } } }
